@import "./typography.scss";

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.541);
    border-radius: 4px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background: #98969669;
}

* {
    box-sizing: border-box;
}

html {
    font-family: montserratRegular, sans-serif;
    line-height: 1.6;
}

body {
    margin: 0;
    width: 100vw;
    overflow-x: hidden;
}

img {
    border-radius: 4px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-weight: normal;
}

h1 {
    font-size: 2.2rem;
    color: white;
    font-family: montserratBold, 'sans-sarif';
}

h2,
h3,
h4 {
    font-family: montserratRegular, 'sans-sarif';
}

h2 {
    font-size: rem(18px);
}

p {
    font-size: rem(14px);
    margin: 0;
    font-family: montserratLight, 'sans-sarif';
}

small {
    margin: 0;
    font-size: rem(12px);
    font-family: montserratExtraLight, 'sans-sarif';
}


button {
    cursor: pointer;
    border: none;
    border-radius: 4px;
    font-family: montserratRegular, 'sans-sarif';
}

textarea,
input {
    resize: none;
    border-radius: 2px;
    border: none;
    font-size: rem(14px);
    font-family: montserratLight, 'sans-sarif';

    &:focus {
        box-shadow: inset 0 0 0 2px #0079bf;
    }
}

label {
    cursor: pointer;
    font-family: montserratBold, 'sans-sarif';
}

a {
    text-decoration: none;
}